

import * as opentype from "opentype.js";

const getResourceUrl = Autodesk.Viewing.Private.getResourceUrl;

import { FONT_LIST, ONDEMAND_FONT_LIST } from "../../res/fonts/font-list";

let FONTS_LOADER;
const avp = Autodesk.Viewing.Private;

class FontEngine {

    constructor() {
        this.fontCache = {};
    }

    loadFont(f) {
        return new Promise((resolve) => {
            opentype.load(getResourceUrl(f.url), (err, font) => {
                if (!font) {
                    const warning = `Failed to load font ${f.name}`;
                    avp.logger.warn(warning);

                    // As we output a warning and not throw an error, we still want to resolve.
                    resolve(warning);
                    return;
                }

                font.pathCache = {};

                this.fontCache[f.name] = font;

                f.subs.forEach(s => {
                    this.fontCache[s] = font;
                });

                resolve();
            });
        });
    }

    loadFonts() {

        if (FONTS_LOADER)
            return FONTS_LOADER;

        let loaders = FONT_LIST.map(f => {
            if (f.url) {
                return this.loadFont(f);
            }
        });

        FONTS_LOADER = Promise.all(loaders);
        return FONTS_LOADER;
    }

    mapFont(font) {
        let cachedFont = this.fontCache[font.name.toLowerCase()];
        if (!cachedFont)
            cachedFont = this.fontCache[font.loadedName.toLowerCase()];
        if (!cachedFont)
            cachedFont = this.fontCache[font.fallbackName.toLowerCase()];

        if (!cachedFont) {
            avp.logger.warn("Failed to map font", font.name);
        }

        return cachedFont;
    }

    drawText(ctx, text, x, y, font, fontSize) {

        let cachedFont = this.mapFont(font);

        if (!cachedFont)
            return;

        if (text.length === 1) {

            let perSizeCache = cachedFont.pathCache[fontSize];

            let path = perSizeCache && perSizeCache[text];

            if (!path) {
                path = cachedFont.getPath(text, 0, 0, fontSize);
                path.fill = null;
                path.stroke = null;

                if (!perSizeCache) {
                    perSizeCache = cachedFont.pathCache[fontSize] = {};
                }
                perSizeCache[text] = path;
            }

            ctx.beginPath(text, x, y, font, fontSize);
            for (let i = 0; i < path.commands.length; i += 1) {
                const cmd = path.commands[i];
                if (cmd.type === 'M') {
                    ctx.moveTo(cmd.x + x, cmd.y + y);
                } else if (cmd.type === 'L') {
                    ctx.lineTo(cmd.x + x, cmd.y + y);
                } else if (cmd.type === 'C') {
                    ctx.bezierCurveTo(cmd.x1 + x, cmd.y1 + y, cmd.x2 + x, cmd.y2 + y, cmd.x + x, cmd.y + y);
                } else if (cmd.type === 'Q') {
                    ctx.quadraticCurveTo(cmd.x1 + x, cmd.y1 + y, cmd.x + x, cmd.y + y);
                } else if (cmd.type === 'Z') {
                    ctx.closePath();
                }
            }

        } else {
            //TODO: No caching is attempted here. This code path should not be
            //called during normal PDF loading. If it every is, it needs to be optimized as above.
            avp.logger.warn("Multi-character text rendering not fully implemented");
            cachedFont.draw(ctx, text, x, y, fontSize);
        }



    }

    getOnDemandFont(name) {
        for (let i = 0; i < ONDEMAND_FONT_LIST.length; i++) {
            const entry = ONDEMAND_FONT_LIST[i];
            if (entry.name === name || entry.subs.includes(name.toLowerCase())) {
                return entry;
            }
        }
    }
}


export const theFontEngine = new FontEngine();

