import * as PDFJS from "@adsk/pdfjs-dist";

import { theFontEngine } from "./font-engine";


// Delay-init variables that require Autodesk.Extension.CompGeom
let LmvCanvasContext = null;
let isRef = null;
let refKey = null;
const MOBILE_DIM_CAP = 2048;
const FIREFOX_DIM_CAP = 4096;
const DEFAULT_DIM_CAP = 8192;

const initCompGeom = () => {
    LmvCanvasContext = Autodesk.Extensions.CompGeom.LmvCanvasContext;
    isRef  = LmvCanvasContext.isRef;
    refKey = LmvCanvasContext.refKey;
};

// LMV-5060 When user change model, we don't know the intent why?
// User might navigate to another page in the same PDF or trying to load a new different model
// In order to save loading time, we need to use this object try to defer the destroy method of previous PDF worker
// Then we can try to avoid memory leak in the meantime to make it much faster to navigate between pages
const PDFWorkingReferenceMap = {};
const DefferedDestroyTime = 3000; // 3 seconds

const av = Autodesk.Viewing;
const avp = av.Private;

/** @constructor */
export function PDFLoader(parent) {

    initCompGeom();

    this.isPdfLoader = true; // For QA only
    this.viewer3DImpl = parent;
    this.setGlobalManager(this.viewer3DImpl.globalManager);
    this.loading = false;
    this.tmpMatrix = new THREE.Matrix4();

    this.logger = avp.logger;
    this.loadTime = 0;
    this.notifiesFirstPixel = true;

    // time stamp when the first mesh was received
    this.t1_firstPixel = undefined;

    this.dtor = this.dtor.bind(this);
    this.viewer3DImpl.api.addEventListener(av.VIEWER_UNINITIALIZED, this.dtor, { once: true });
}

av.GlobalManagerMixin.call(PDFLoader.prototype);

/**
 * BubbleNode Structure for PDF data will help to do page navigation
 * @param {PDF} pdf PDF object from pdf.js
 */
PDFLoader.prototype.createPDFDocument = function (pdf) {
    var numPages = pdf.numPages;
    var guid = pdf.fingerprint;
    var me = this;
    // Let the generator stay along with document lifecycle, it will give us a chance to maximum the caching
    // to get the best performance
    var parent = {
        guid: guid,
        type: "design",
        "hasThumbnail": "false",
        "progress": "complete",
        "status": "success",
        "success": "100%",
        "name": "PDF",
        "isVectorPDF": true,
        "role": "viewable",
        totalRasterPixels: 0,
        getPDF: function () {    // this can avoid issue in searilization
            if (pdf && pdf._transport && !pdf._transport.destroyed) {
                return pdf;
            } else {
                return null;
            }
        }
    };

    var children = [];

    for (var i = 1; i <= numPages; i++) {
        children.push({
            guid: guid + "/" + i,
            type: "geometry",
            role: "2d",
            status: "success",
            progress: "complete",
            viewableID: guid + "/" + i,
            name: av.i18n.translate("Page %(i)", { i }),
            page: i,
            children: [{
                role: av.BubbleNode.PDF_PAGE_NODE.role,
                page: i,
                type: "resource",
                status: "success",
                progress: "complete",
                urn: me.svfUrn
            }]
        });
    }

    parent.children = children;
    return new av.Document(parent);
};


PDFLoader.prototype.dtor = function () {
    if (this.svf && this.svf.propDbLoader) {
        this.svf.propDbLoader.dtor();
        this.svf.propDbLoader = null;
    }

    this.currentLoadPath = null;
    this.isf2d = undefined;
    this.viewer3DImpl.api.removeEventListener(av.VIEWER_UNINITIALIZED, this.dtor);

    if (this.pdf) {
        // Reset the flag synchronized here
        delete PDFWorkingReferenceMap[this.pdf.fingerprint];
        setTimeout(() => {
            // If the PDF is used in DefferedDestroyTime time, the PDF will be still alive
            // Otherwise the pdf worker is gonna be destroyed, and the document has been load again.
            if (!PDFWorkingReferenceMap[this.pdf.fingerprint]) {
                this.pdf.destroy();
                this.pdf = null;
            }
        }, DefferedDestroyTime);
    }

    if (this._renderTask) {
        this._renderTask.cancel();
        this._renderTask = null;
    }

    this.svf = null;
    this.options = null;
    avp.logger.log("PDFLoader destroy");
};


PDFLoader.prototype.loadFile = function (path, options, onSuccess, onWorkerStart) {
    if (this.loading) {
        avp.logger.log("Loading of PDF already in progress. Ignoring new request.");
        return false;
    }
    this.loading = true;

    const urnIdx = path.indexOf('urn:');
    if (urnIdx !== -1) {
        // Extract urn:adsk.viewing:foo.bar.whateverjunks out of the path URL and bind it to logger.
        // From now on, we can send logs to viewing service, and logs are grouped by urn to make Splunk work.
        path = decodeURIComponent(path);
        const urn = path.substr(urnIdx, path.substr(urnIdx).indexOf('/'));
        avp.logger.log("Extracted URN: " + urn);

        // Extract urn(just base64 code)
        const _index = urn.lastIndexOf(':');
        this.svfUrn = urn.substr(_index + 1);

        //V2 only accepts URL encoded paths
        const qIdx = path.indexOf("?", urnIdx);
        if (qIdx !== -1) {
            path = path.slice(0, urnIdx) + encodeURIComponent(path.slice(urnIdx, qIdx)) + path.slice(qIdx);
        }
        else {
            path = path.slice(0, urnIdx) + encodeURIComponent(path.slice(urnIdx));
        }
    } else {
        this.svfUrn = path;
    }

    this.sharedDbPath = options.sharedPropertyDbPath;
    this.currentLoadPath = path;
    this.acmSessionId = options.acmSessionId;

    //This is done to avoid CORS errors on content served from proxy or browser cache
    //The cache will respond with a previously received response, but the Access-Control-Allow-Origin
    //response header might not match the current Origin header (e.g. localhost vs. developer.api.autodesk.com)
    //which will cause a CORS error on the second request for the same resource.
    this.queryParams = [
        this.acmSessionId && `acmsession=${this.acmSessionId}`,
        av.endpoint.getQueryParams(),
    ].filter(p => p).join('&');

    this.options = options;

    if (this.options.placementTransform) {
        //NOTE: The scale of the placement transform is not always sufficient to
        //determine the correct scale for line widths. This is because when a 2D model (in inches) is
        //loaded into a 3d scene in feet, the transform includes all the scaling needed to get into feet
        //but the model space line weight for the drawing is relative to the drawing itself, so an extra
        //factor of 12 would be needed in such case to cancel out the 1/12 needed for inch->foot.
        //This could probably be automatically derived, but in an error prone way, so I'm leaving it
        //up to the application layer that does the model aggregation to pass in the right model scale as an option.
        this.modelScale = this.options.modelScale || this.options.placementTransform.getMaxScaleOnAxis();
    } else {
        this.modelScale = this.options.modelScale || 1;
    }

    var scope = this;

    scope.loadFydoCB(path, options, onSuccess, onWorkerStart);

    return true;
};

PDFLoader.prototype.getDocument = function () {
    return this.options.bubbleNode.getRootNode().data;
};

PDFLoader.prototype.getFontGenerator = function () {
    return this.viewer3DImpl.api.loadExtension("Autodesk.MSDF").then(() => {
        const generator = new Autodesk.MSDF.Generator();
        generator.setGlobalManager(this.globalManager);
        return generator;
    });
};

PDFLoader.prototype.createFontAtlas = function (page, cacheKey) {
    if (PDFLoader.enableMSDFText) {
        // Fetching the operation list will trigger the font loading
        // The loaded font event will be trigger in next tick
        // So we need use timeout to let the app continue
        return new Promise((resolve, reject) => {
            this.getOperatorList(page).then(() => {
                setTimeout(() => {
                    this.getFontGenerator().then((generator) => {
                        generator.createFontAtlasForPDF(page, cacheKey).then((fontAtlas) => {
                            resolve(fontAtlas);
                        });
                    });
                }, 0);
            });
        });
    } else {
        return Promise.resolve();
    }
};

PDFLoader.prototype.evaluatePageLineStyles = function(page) {
    return this.getOperatorList(page).then((data) => {
        let w = 0;
        let cacheSet = new Set();

        for(var i = 0; i < data.fnArray.length; i++) {
            if(data.fnArray[i] == PDFJS.OPS.setDash) {
                w = Math.max(w, data.argsArray[i][0].length);
                let key = data.argsArray[i].join("/");
                cacheSet.add(key);
            }
        }

        let h = cacheSet.size + 1;
        this.pageLineStyleParams = {
            width: w,
            height: h
        };
    });
};

PDFLoader.prototype.detectSmallImageSequences = function (page) {
    const MAX_OP_DISTANCE = 12;
    const MIN_OPS = 5*MAX_OP_DISTANCE;
    const MAX_PAGE_UNIT_DISTANCE = 100;

    //console.time("detectSmallImageSequences");
    return this.getOperatorList(page).then((data) => {

        function isSmallImage(index) {
            const op = data.fnArray[index];
            if (op === PDFJS.OPS.paintJpegXObject || op === PDFJS.OPS.paintImageXObject) {
                const w = data.argsArray[index][1];
                const h = data.argsArray[index][2];
                return (w <= 2 || h <= 2);
            }
            if (op === PDFJS.OPS.paintImageMaskXObject || op === PDFJS.OPS.paintInlineImageXObject) {
                const w = data.argsArray[index][0].width;
                const h = data.argsArray[index][0].height;
                return (w <= 2 || h <= 2);
            }
            return false;
        }

        function findSequenceStart(index) {
            // scan backwards for at most MAX_OP_DISTANCE ops and find the first PDFJS.OPS.save
            // on the way, no PDFJS.OPS.restore or anything text-related must be found
            // this logic is supposed to detect cases like (index points to "image")
            //   - save transform image restore
            //   - save transform constructpath clip endpath save transform image restore restore
            const end = Math.max(-1, index-MAX_OP_DISTANCE);
            let i = index-1;
            let q = -1; // if no PDFJS.OPS.save is found, -1 will signal "no sequence"
            let qCount = 0;
            for (; i > end; i--) {
                const op = data.fnArray[i];
                switch (op) {
                    case PDFJS.OPS.save:
                        q = i;
                        qCount++;
                        break;
                    case PDFJS.OPS.showText:
                    case PDFJS.OPS.showSpacedText:
                    case PDFJS.OPS.nextLineShowText:
                    case PDFJS.OPS.nextLineSetSpacingShowText:
                    case PDFJS.OPS.restore:
                        i = end;
                        break;
                }
            }

            return {q, qCount};
        }

        function findSequenceEnd(index, qCount) {
            // scan forwards for at most MAX_OP_DISTANCE ops and find the qCount'th PDFJS.OPS.restore
            const end = Math.min(data.fnArray.length, index+MAX_OP_DISTANCE);
            let i = index+1;
            let q = index+1;
            for (; i < end && qCount>0; i++) {
                const op = data.fnArray[i];
                if (op == PDFJS.OPS.restore) {
                    q = i;
                    qCount--;
                }
            }

            // if not enough PDFJS.OPS.restore are found, mark sequence as broken
            return qCount>0 ? -1 : q;
        }

        function addPointBBox(bbox, x, y) {
            bbox[0] = Math.min(bbox[0], x);
            bbox[1] = Math.min(bbox[1], y);
            bbox[2] = Math.max(bbox[2], x);
            bbox[3] = Math.max(bbox[3], y);
        }

        function addImageBBox(bbox, xform) {
            // images use a fixed [0, 0]x[1, 1] space => transform each corner and add to bbox
            // xform uses the canvas context convention: https://developer.mozilla.org/en-US/docs/Web/API/CanvasRenderingContext2D/transform
            addPointBBox(bbox,                               xform[4],                               xform[5]);
            addPointBBox(bbox,                1 * xform[2] + xform[4],                1 * xform[3] + xform[5]);
            addPointBBox(bbox, 1 * xform[0]                + xform[4], 1 * xform[1]                + xform[5]);
            addPointBBox(bbox, 1 * xform[0] + 1 * xform[2] + xform[4], 1 * xform[1] + 1 * xform[3] + xform[5]);
        }

        function process() {
            let sequenceStart = {q: -1, qCount: 0};
            let lastSmallImage = -1;
            let smallImageSequences = [];
            let currXform = [1, 0, 0, 1, 0, 0];
            let xformStack = [];
            let bbox = [1e10, 1e10, -1e10, -1e10];  // [minx, miny, maxx, maxy]

            const beginSequence = function(i) {
                sequenceStart = findSequenceStart(i);
                lastSmallImage = i;
                bbox = [1e10, 1e10, -1e10, -1e10];
            };

            const endSequence = function() {
                const end = findSequenceEnd(lastSmallImage, sequenceStart.qCount);
                // if the sequence is too short, ignore it (performance gain would be too small)
                if (end >= 0 && end > sequenceStart.q + MIN_OPS) {
                    // sequence ended correctly
                    smallImageSequences.push({
                        start: sequenceStart.q,
                        end,
                        bbox
                    });
                }
            };

            for (let i = 0; i < data.fnArray.length; i++) {
                const op = data.fnArray[i];
                if (op == PDFJS.OPS.save) {
                    xformStack.push(currXform);
                    currXform = currXform.slice(0, 6);
                } else if (op == PDFJS.OPS.transform) {
                    const m = currXform;
                    const a = data.argsArray[i];
                    currXform = [ m[0] * a[0] + m[2] * a[1],
                                  m[1] * a[0] + m[3] * a[1],
                                  m[0] * a[2] + m[2] * a[3],
                                  m[1] * a[2] + m[3] * a[3],
                                  m[0] * a[4] + m[2] * a[5] + m[4],
                                  m[1] * a[4] + m[3] * a[5] + m[5] ];
                } else if (op == PDFJS.OPS.restore) {
                    currXform = xformStack.pop() || [1, 0, 0, 1, 0, 0];
                } else if (isSmallImage(i)) {
                    // is this a new sequence?
                    if (sequenceStart.q < 0) {
                        beginSequence(i);
                    } else if (i > lastSmallImage + MAX_OP_DISTANCE) {
                        // too many operators between the previous small image and the current -> end sequence and start a new one
                        endSequence();
                        beginSequence(i);
                    } else {
                        // check if the image is too far away from last image (i.e., it should start new sequence)
                        let bboxImg = [1e10, 1e10, -1e10, -1e10];
                        addImageBBox(bboxImg, currXform);
                        // skip the check for the first few images as the sequence needs to establish a reasonable line length first
                        if (i - sequenceStart.q > MIN_OPS &&
                            (bboxImg[2] < bbox[0] - MAX_PAGE_UNIT_DISTANCE || bboxImg[0] > bbox[2] + MAX_PAGE_UNIT_DISTANCE ||
                             bboxImg[3] < bbox[1] - MAX_PAGE_UNIT_DISTANCE || bboxImg[1] > bbox[3] + MAX_PAGE_UNIT_DISTANCE))  {

                            // end sequence and start a new one
                            endSequence();
                            beginSequence(i);
                        } else {
                            // just add to current sequence
                            lastSmallImage = i;
                        }
                    }

                    if (lastSmallImage >= 0) {
                        addImageBBox(bbox, currXform);
                    }
                }
            }

            // end last sequence
            if (lastSmallImage >= 0) {
                endSequence();
            }

            return smallImageSequences;
        }

        this.smallImageSequences = process();
        //console.timeEnd("detectSmallImageSequences");
    }).catch(avp.logger.log);
};

PDFLoader.prototype.fixTilingPatternsWithHugeGaps = function (page) {
    const MAX_GAP_FACTOR = 10;
    const BUFFER_FACTOR = 2;

    // addresses BLMV-7214 and VIZX-191
    return this.getOperatorList(page).then((data) => {
        for (let i = 0; i < data.fnArray.length; i++) {
            // search for tiling pattern definitions
            const op = data.fnArray[i];
            if (op != PDFJS.OPS.setFillColorN) { continue; }
            let args = data.argsArray[i];
            if (!args || args.length < 7) { continue; }
            if (args[0] != "TilingPattern") { continue; }

            // look at the pattern parameters
            const bbox = args[4];
            const xstep = args[5];
            const ystep = args[6];

            const bboxWidth = bbox[2] - bbox[0];
            const bboxHeight = bbox[3] - bbox[1];

            // if step is more than MAX_GAP_FACTOR times the size of the bbox, assume that there is no actual tiling intended
            if (xstep > MAX_GAP_FACTOR*bboxWidth && ystep > MAX_GAP_FACTOR*bboxHeight) {
                // ideally, we would then use a texture without repeat, but there is no such option in our materials
                // instead, add a bit of buffer in case some area outside the pattern bbox is visible
                args[5] = BUFFER_FACTOR * bboxWidth;
                args[6] = BUFFER_FACTOR * bboxHeight;
            }
        }
    });
};

/**
 * When we do synchronized loading pdf referenced object, it might return empty if that content was not loaded
 * Asynchronized way is better, but it increases the complexity of loading it
 * This function is prefly for parsing those references
 */
PDFLoader.prototype.loadMarkedContentPropertiesReferences = function (pdf, page) {
    var referenceObjMap = {};

    function getRefObj(ref) {
        return pdf.getObject(ref).then((data) => {
            referenceObjMap[refKey(data.key)] = data.value;
            // loop in one more level for dictionary
            var childPromises = [];
            if (data.isDictionary) {
                for (let itemKey in data.value) {
                    if (isRef(data.value[itemKey])) {
                        if (referenceObjMap[refKey(data.value[itemKey])]) {
                            data.value[itemKey] = referenceObjMap[refKey(data.value[itemKey])];
                        } else {
                            childPromises.push(pdf.getObject(data.value[itemKey]).then((data1) => {
                                data.value[itemKey] = data1.value;
                                referenceObjMap[refKey(data1.key)] = data1.value;
                            }).catch(avp.logger.log));
                        }
                    }
                }
            }

            return Promise.all(childPromises);
        }).catch(avp.logger.log);     // for any missing reference object, just ignore and continue
    }

    return this.getOperatorList(page).then((ops) => {
        var promises = [];
        for (var i = 0; i < ops.fnArray.length; i++) {

            if (ops.fnArray[i] == PDFJS.OPS.beginMarkedContent || ops.fnArray[i] == PDFJS.OPS.beginMarkedContentProps) {
                var args1 = ops.argsArray[i];
                for (let j = 0; args1 && j < args1.length; j++) {
                    if (isRef(args1[j])) {
                        let promise = getRefObj(args1[j]);
                        promises.push(promise);
                    } else if (typeof (args1[j]) == "object") {
                        for (let key in args1[j]) {
                            if (isRef(args1[j][key])) {
                                let promise = getRefObj(args1[j][key]);
                                promises.push(promise);
                            }
                        }
                    }
                }
            }
        }

        return Promise.all(promises)
            // This is a nice to have feature, in case of anything wrong, ust ignore and continue
            .catch(avp.logger.log)
            .then(() => {
                return referenceObjMap;
            });
    });
};

PDFLoader.prototype.loadFydoCB = function (path, options, onSuccess, onWorkerStart) {
    this.t0 = Date.now();

    var svfPath = avp.pathToURL(path);
    if (this.queryParams) {
        svfPath += '?' + this.queryParams;
    }

    var scope = this;

    if (onWorkerStart)
        onWorkerStart();

    let cMapUrl = options.cMapUrl;
    let cMapPacked = options.cMapPacked;
    if (!cMapUrl) {
        cMapUrl = avp.getResourceUrl('extensions/PDF/cmaps/');
        cMapPacked = true;
    }

    var params = {
        url: svfPath,
        disableFontFace: true,
        stopAtErrors: false,
        isEvalSupported: false,
        cMapUrl,
        cMapPacked,
    };

    if (av.endpoint.getUseCookie()) {
        params.withCredentials = true;
    } else {
        params.httpHeaders = av.endpoint.HTTP_REQUEST_HEADERS;
    }

    this.viewer3DImpl._signalNoMeshes();

    let fe = theFontEngine;

    //Start loading fonts.
    let loadFontsAsync = fe.loadFonts();

    function getInternalLinks(pdf, page) {
        return page.getAnnotations().then(annotations => {
            if (annotations && annotations.length > 0) {
                return Promise.all(annotations.map((annotation) => {
                    // internal page link
                    if (annotation.subtype == "Link" && !annotation.url) {
                        var pageIndexPromise;

                        // So far I found 2 ways of annotion destination, it might be more, extend this part if we have new case
                        if (typeof (annotation.dest) == "string") {
                            pageIndexPromise = pdf.getDestination(annotation.dest).then((destInfo) => {
                                return pdf.getPageIndex(destInfo[0]);
                            });
                        } else {
                            if (annotation.dest instanceof Array) {
                                pageIndexPromise = pdf.getPageIndex(annotation.dest[0]);
                            }
                        }
                        if (pageIndexPromise) {
                            return pageIndexPromise.then((pageIndex) => {
                                // need to register the rect to pageIndex
                                // pdf getPage function start from 1, so here need add extra 1
                                annotation.pageIndex = pageIndex + 1;
                                return annotation;
                            });
                        }
                    } else {
                        // external link, will add support here, if needed
                    }
                })).then((internalLinks) => {
                    return internalLinks.filter((item) => item != undefined);
                }).catch((error) => {
                    // If we have difficulty of parsing annotations, we should let the loading continue
                    return [];
                });
            }
        });
    }

    function updateLinkBounds(renderContext, viewport, internalLinks) {
        var dbIdLinks = [];
        // Use negative dbId since hotArea is added by us
        var dbId = -2;
        var transform = renderContext.getCurrentTransform();
        for (var i = 0; internalLinks && i < internalLinks.length; i++) {
            const rect = createRectByViewportTransform(renderContext, transform, viewport, internalLinks[i].rect);
            var linkNode = scope.svf.loadOptions.bubbleNode.getRootNode().children[internalLinks[i].pageIndex - 1];
            dbIdLinks.push({
                dbId: dbId--,
                properties: [{
                    displayValue: linkNode.data.viewableID
                }],
                box: [
                    Math.min(rect[0], rect[2]),
                    Math.min(rect[1], rect[3]),
                    Math.max(rect[0], rect[2]),
                    Math.max(rect[1], rect[3])
            ]
            });
        }
        scope.dbIdLinks = dbIdLinks;
    }

    function createRectByViewportTransform(renderContext, transform, viewport, tempRect) {
            let rect = viewport.convertToViewportRectangle(tempRect);
            rect = [renderContext.tx(rect[0], rect[1], transform),
                renderContext.ty(rect[0], rect[1], transform),
                renderContext.tx(rect[2], rect[3], transform),
                renderContext.ty(rect[2], rect[3], transform)];
        return rect;
    }

    function rotateAround(p, center, angle) {
        let c = Math.cos( angle );
        let s = Math.sin( angle );

        let x = p.x - center.x;
        let y = p.y - center.y;

        p.x = x * c - y * s + center.x;
        p.y = x * s + y * c + center.y;
    }

    /**
     * Format strings output from PDF page
     * @param {Object} renderCtx - Render context of current page rendering.
     * @param {Object} pageInfo - dimensions of current page.
     * @param {Object} viewport - Viewport based on Page properties.
     * @param {Array} vectorStrings - Array with all strings from Vector PDF.
     * @param {Object} formattedGlyphs - Object which contains glyphs and their properties.
     * @returns {Object} - Object with list of strings and their stringBoxes.
     */
    function formatVectorPdfStrings(renderCtx, pageInfo, viewport, vectorStrings, formattedGlyphs) {
        const strings = [];
        const stringBoxes = [];
        const stringAngles = [];
        const stringCharWidths = [];
        const stringPositions = [];
        const stringHeights = [];
        const stringWidths = [];
        const renderCtxTransform = renderCtx.viewport.transform;
        const currentTransform = renderCtx.getCurrentTransform();

        // Transform function that combines values from Render Context Transform and Transform of a string
        const transformFunc = (m1, m2) => {
            return [
                m1[0] * m2[0] + m1[2] * m2[1],
                m1[1] * m2[0] + m1[3] * m2[1],
                m1[0] * m2[2] + m1[2] * m2[3],
                m1[1] * m2[2] + m1[3] * m2[3],
                m1[0] * m2[4] + m1[2] * m2[5] + m1[4],
                m1[1] * m2[4] + m1[3] * m2[5] + m1[5],
            ];
        };

        //Strings in PDF.js are grouped, hence 2 loops needed to extract every expression.
        for(let i = 0; i < vectorStrings.length; i++) {
            if (vectorStrings[i].items) {
                const items = vectorStrings[i].items;

                for (let j = 0; j < items.length; j++) {
                    const tempObject = items[j];
                    const tempCharWidth = [];
                    // tempObject contains characters map with corresponding width values for each char
                    // formattedGlyphs stores font families by name and char and widths
                    for (let k = 0; k < tempObject.str.length; k++) {
                        const tempFontName = formattedGlyphs[tempObject.fontName];
                        const tempString = tempObject.str[k];
                        // Fix for BLMV-5151, need to verify that specific character exist, otherwise it can prevent PDF from loading
                        if (!tempFontName[tempString]) {
                            console.warn(`Cannot find ${tempString}'s width in glyph dictionary`);
                            tempCharWidth.push(0);
                        } else {
                            tempCharWidth.push(tempFontName[tempString].width);
                        }
                    }
                    stringCharWidths.push(tempCharWidth);
                    const textWidth = tempObject.width;
                    const textHeight = tempObject.height;

                    // Get bounding box's angle of a string by using viewport and strings transform
                    // Rotation of bbox applied on UI side based on angle
                    const strTx = transformFunc(renderCtxTransform, tempObject.transform);
                    const strAngle = Math.atan2(strTx[1], strTx[0]);

                    // Creating Bounding box based on transform values from pdf.js object
                    // 4-th place in array is vertical offset of string
                    // 5-th place in array is horizontal offset of string
                    const minX = tempObject.transform[4];
                    const maxX = tempObject.transform[4] + textWidth;
                    const minY = tempObject.transform[5];
                    const maxY = tempObject.transform[5] + textHeight;

                    const box = new THREE.Box2().setFromPoints([ new THREE.Vector2(minX, minY), new THREE.Vector2(maxX, maxY)]);
                    rotateAround(box.max, box.min, THREE.Math.degToRad(pageInfo.rotate));

                    const tempRect = [box.min.x, box.min.y, box.max.x, box.max.y];

                    const rect = createRectByViewportTransform(renderCtx, currentTransform, viewport, tempRect);

                    strings.push(tempObject.str);
                    stringBoxes.push(rect[0], rect[1], rect[2], rect[3]);
                    stringAngles.push(strAngle);
                    stringPositions.push([rect[0], rect[1]]);
                    stringHeights.push(textHeight);
                    stringWidths.push(textWidth);
                }
            }
        }
        return {strings, stringBoxes, stringAngles, stringCharWidths, stringPositions, stringHeights, stringWidths};
    }

    /**
     * Extracts the viewports from the PDF
     * @param {PDFPageProxy} page
     * @param {LmvCanvasContext} renderContext
     * @returns {Object[]} The extracted viewports
     */
    PDFLoader.prototype.buildViewports = function(page, renderContext) {
        let viewports = [];
        return this.getOperatorList(page).then((ops) => {
            for (let i = 0; i < ops.fnArray.length; i++) {

                if (ops.fnArray[i] == PDFJS.OPS.beginMarkedContent || ops.fnArray[i] == PDFJS.OPS.beginMarkedContentProps) {
                    if (ops.argsArray[i]) {
                        let properties = ops.fnArray[i] == PDFJS.OPS.beginMarkedContent ? ops.argsArray[i][0] : ops.argsArray[i][1];

                        if(properties) {
                            properties = renderContext._processProperties(properties);

                            if(properties.VP) {
                                const vp = renderContext._getModelToViewportMatrix(properties.VP, !!properties.UNITS);
                                viewports.push(renderContext.createViewPortData(vp, properties.UNITS, properties.PRECISION));
                            }
                        }
                    }
                }
            }
            return viewports;
        });
    };

    function buildLayers(page) {
        return scope.getOperatorList(page).then((ops) => {
            let fnArray = ops.fnArray;
            let argsArray = ops.argsArray;
            let layers = {};
            let layerStartIndex = 1;

            for (var i = 0; fnArray && i < fnArray.length; i++) {
                let fn = fnArray[i];
                if (fn === PDFJS.OPS.beginMarkedContentProps) {
                    if (argsArray[i] && argsArray[i][0] == "OC" && argsArray[i][1]!==null) {
                        const key = LmvCanvasContext.getLayerKey(argsArray[i][1]);
                        if (key!==undefined && layers[key] === undefined) {
                            layers[key] = layerStartIndex++;
                        }
                    }
                }
            }
            return layers;
        })
            // It should not have error, but we don't test enough for PDF
            // In case of potential error happened, we still want to show the contents
            .catch(avp.logger.log)
            .then((layers) => {
                return scope.pdf.getPageOCGNames(page.ref)
                    .catch(avp.logger.log)
                    .then(layerNameMap => {
                        layerNameMap = layerNameMap || {};
                        var layersRoot = { name: 'root', id: 'root', children: [], isLayer: false };
                        var layersMap = { "0": 0 };
                        var layerCount = 1;
                        for (var layerKey in layers) {
                            var l = layerNameMap[layerKey];
                            var name = typeof l === 'object' && Object.prototype.hasOwnProperty.call(l, 'name') ? l.name : layerKey;

                            var layer = {
                                name: name,
                                id: 'group-' + layers[layerKey],
                                isLayer: true,
                                index: layers[layerKey],
                                visible: l?.visible ?? true,
                                children: []
                            };
                            // for layers, the first drawing layer should be at the bottom of the list
                            layersRoot.children.unshift(layer);
                            layerCount++;
                            layersMap[layers[layerKey]] = layers[layerKey];
                        }

                        return {
                            layerCount,
                            layersRoot,
                            layersMap,
                            layers
                        };
                    });
            });
    }

    function getStrings(page) {
        const documentStrings = [];
        const readableStream = page.streamTextContent({ normalizeWhitespace: true, combineTextItems: true });
        return new Promise((resolve, reject) => {
            let pump = () => {
                reader.read().then(({ value, done }) => {
                    if (done) {
                        resolve(documentStrings);
                        return;
                    }
                    documentStrings.push(value);
                    pump();
                }, reject);
            };
            const reader = readableStream.getReader();
            pump();
        });
    }

    function renderPage(pageNumber) {
        var pdf = scope.pdf;
        PDFWorkingReferenceMap[pdf.fingerprint] = 1;

        if (pageNumber <= 0 || pageNumber > pdf.numPages) {
            pageNumber = 1;
        }

        if (scope.model) {
            scope.viewer3DImpl.unloadModel(scope.model);
            scope.model = undefined;
        }
        var context = {};
        let documentStrings = [];
        return pdf.getPage(pageNumber).then(page => {
            return scope.createFontAtlas(page, pdf.fingerprint).then((fontAtlas) => {
                context.fontAtlas = fontAtlas;
                return getInternalLinks(pdf, page).then((internalLinks) => {
                    context.internalLinks = internalLinks;
                });
            }).then(() => {
                // Get PDF strings from page
                // options Feature Flag disables by default PDF.js strings fetch
                return options.extendStringsFetching ? getStrings(page) : [];
            }).then((strings) => {
                documentStrings = strings;
                return scope.detectCircle(page);
            }).then(() => {
                // Load any large font files that need to be used by the current PDF.
                return scope.loadOnDemandFonts(page, fe);
            }).then(() => {
                return scope.loadMarkedContentPropertiesReferences(pdf, page).then((map) => {
                    context.pdfRefObjMap = map;
                });
            }).then(() => {
                return scope.evaluatePageLineStyles(page);
            }).then(() => {
                return scope.detectSmallImageSequences(page);
            }).then(() => {
                return scope.fixTilingPatternsWithHugeGaps(page);
            }).then(() => {
                let internalLinks = context.internalLinks;
                var view = page.view;
                view = view.slice(0);

                // LMV-5149: Apply the page rotation to the page_width and page_height
                var rotationMatrix = new THREE.Matrix4().makeRotationZ((page.rotate / 180) * Math.PI);
                var rotatedViewVec1 = new THREE.Vector3(view[0], view[1], 0).applyMatrix4(rotationMatrix);
                var rotatedViewVec2 = new THREE.Vector3(view[2], view[3], 0).applyMatrix4(rotationMatrix);


                // Apply the rotation vector to the view
                // These values are used to calculate the page_width and the page_height
                view[0] = Math.min(rotatedViewVec1.x, rotatedViewVec2.x);
                view[1] = Math.min(rotatedViewVec1.y, rotatedViewVec2.y);
                view[2] = Math.max(rotatedViewVec1.x, rotatedViewVec2.x);
                view[3] = Math.max(rotatedViewVec1.y, rotatedViewVec2.y);

                // need to put a cap on 1 dimention : 8192 for desktop / 4096 for firefox / 2048 for mobile
                // LMV-4731: If we set the viewport too large, it will fail to load in Firefox
                var maxDim = Math.max(view[2] - view[0], view[3] - view[1]);
                const capDim = av.isMobileDevice() ? MOBILE_DIM_CAP : av.isFirefox() ? FIREFOX_DIM_CAP : DEFAULT_DIM_CAP;
                const scaleByDim = capDim / maxDim;

                const DPI = 300;
                //Render the PDF at 300 pixels/inch instead of 72 points/inch.
                //This seems to match what Adobe Reader does at max zoom.
                const scaleByDPI = DPI / 72;

                // scale as much as possible without exceeding capDim
                const pageToCanvasScale = Math.min(scaleByDPI, scaleByDim);

                // Important: the measure tool expects inches
                // take userUnit into account: PDF specs define userUnit as a factor on the size of a pt, so the conversion to inches is not 1/72, but userUnit/72
                const userUnit = page.userUnit || 1;
                const canvasToInchesScale = (userUnit / 72 / pageToCanvasScale);

                // Convert to source file unit's if supplied in the bubble.
                let targetUnits = scope.options.bubbleNode?.getSourceFileUnits() || avp.ModelUnits.INCH;

                const inchesToTargetUnitsScale = Autodesk.Viewing.Private.convertUnits(avp.ModelUnits.INCH, targetUnits, 1, 1);
                const canvasToTargetUnitScale = canvasToInchesScale * inchesToTargetUnitsScale;

                //Negative rotation because we skip the y-flip.
                //TODO: need to check if this is right for all rotations, if not just let it flip y and negate that in the canvas context.
                // viewport will use canvas units
                var options = {
                    scale: pageToCanvasScale,
                    rotation: -page.rotate,
                    dontFlip: true
                };
                var viewport = page.getViewport(options);
                viewport.clipToViewport = true;

                scope.renderContext = new LmvCanvasContext(viewport, canvasToTargetUnitScale, scope.processReceivedMesh2D.bind(scope), fe, PDFLoader.useTextLayer, context.fontAtlas, context.pdfRefObjMap);

                // update lineStyles
                scope.renderContext.setLineStyleParam(scope.pageLineStyleParams);
                scope.viewer3DImpl.matman().setLineStyleTexture(scope.renderContext.lineStyleTexture);
                scope.renderContext.setSmallImageSequences(scope.smallImageSequences);

                scope.renderContext.setCircleInfo(scope.circleInfo);

                // Assign consecutive dbIds. The only purpose of these IDs is to split the page down into smaller shapes, so that
                // we can use the ID buffer to quickly find shapes for snapping.
                scope.renderContext.consecutiveIds = true;

                const bounds = scope.renderContext.bounds;

                scope.svf = {
                    is2d: true,
                    isPdf: true,
                    viewports: [],
                    layersMap: { "0": 0 },
                    layerCount: 1,
                    bbox: new THREE.Box3(new THREE.Vector3(bounds.x, bounds.y, 0), new THREE.Vector3(bounds.z, bounds.w, 0)),
                    metadata: {
                        page_dimensions: {
                            page_width: (view[2] - view[0]) * inchesToTargetUnitsScale * userUnit / 72,
                            page_height: (view[3] - view[1]) * inchesToTargetUnitsScale * userUnit / 72,
                            logical_width: viewport.width * inchesToTargetUnitsScale,
                            logical_height: viewport.height * inchesToTargetUnitsScale,
                            logical_offset_x: viewport.offsetX * inchesToTargetUnitsScale,
                            logical_offset_y: viewport.offsetY * inchesToTargetUnitsScale,
                            page_units: targetUnits,
                            rotation: viewport.rotation
                        },
                        currentPage: pageNumber
                    },
                    placementTransform: scope.options.placementTransform?.clone(),
                    placementWithOffset: scope.options.placementTransform?.clone(),
                    strings: [],
                    stringDbIds: [],
                    getPDF: function () {
                        return scope.pdf;
                    }
                };

                var t0 = performance.now();

                // layers information is required before we call onSuccess
                buildLayers(page).then((layersInfo) => {
                    scope.svf.layersRoot = layersInfo.layersRoot;
                    scope.svf.layerCount = layersInfo.layerCount;
                    scope.svf.layersMap = layersInfo.layersMap;
                    scope.renderContext.layers = layersInfo.layers;
                })
                    // Ignore any potential error and let the rendering continue
                    .catch(avp.logger.log)
                    .then(() => scope.buildViewports(page, scope.renderContext))
                    .then((viewports) => {
                        // store viewports
                        scope.svf.viewports = scope.renderContext.viewports = scope.renderContext.viewports.concat(viewports);

                        if (scope.currentLoadPath === null) {
                            const errorMsg = "PDF loader was destroyed";
                            onSuccess && onSuccess(errorMsg);
                            return Promise.reject(errorMsg);
                        }

                        scope.onModelRootLoadDone(scope.svf);
                        if (onSuccess) {
                            onSuccess(null, scope.model);
                        }
                        scope.viewer3DImpl.api.fireEvent({ type: av.MODEL_ROOT_LOADED_EVENT, svf: scope.svf, model: scope.model });
                    }).then(() => {
                        //
                        // Render PDF page into canvas context
                        //
                        loadFontsAsync.then(() => {
                            var renderTask = scope._renderTask = page.render(scope.renderContext, this);
                            renderTask.promise.then(() => {
                                if (internalLinks && internalLinks.length > 0) {
                                    // render hot area in the canvas to enable internal links
                                    // Need to render the hotArea after main page was rendered,
                                    // because extra dbID will be assigned to hotArea
                                    if (PDFLoader.enableHyperlinks) {
                                        updateLinkBounds(scope.renderContext, viewport, internalLinks);
                                        scope.svf.metadata.hyperLinks = scope.dbIdLinks;
                                        scope.viewer3DImpl.api.loadExtension("Autodesk.Hyperlink");
                                    }
                                }

                                // Format PDF document strings
                                if (documentStrings?.length > 0) {
                                    const pdfObjects = page?.commonObjs?._objs;
                                    const formattedGlyphs = {};
                                    // pdfObjects contain information regarding fonts and their properties as size, width, style etc.
                                    // By iterating this object, we can obtain compact object with relevant characters for further calculations
                                    // Final structure of formattedGlyphs object: Family Name-> Character -> character width
                                    for (let pdfObject in pdfObjects) {
                                        if (pdfObjects[pdfObject].data?.glyphCache) {
                                            const glyphCache =  pdfObjects[pdfObject].data.glyphCache;
                                            const fontName = pdfObjects[pdfObject].data.loadedName;
                                            const tempGlyphs = {};
                                            for (let char in glyphCache) {
                                                const unicodeChar = glyphCache[char].unicode;
                                                tempGlyphs[unicodeChar] = {
                                                    width: glyphCache[char].width,
                                                };
                                            }
                                            formattedGlyphs[fontName] = Object.assign({}, tempGlyphs, formattedGlyphs[fontName]);
                                        }
                                    }

                                    const formattedStrings = formatVectorPdfStrings(scope.renderContext, page._pageInfo, viewport, documentStrings, formattedGlyphs);
                                    scope.svf.strings = formattedStrings.strings;
                                    scope.svf.stringBoxes = formattedStrings.stringBoxes;
                                    scope.svf.stringAngles = formattedStrings.stringAngles;
                                    scope.svf.stringCharWidths = formattedStrings.stringCharWidths;
                                    scope.svf.stringPositions = formattedStrings.stringPositions;
                                    scope.svf.stringHeights = formattedStrings.stringHeights;
                                    scope.svf.stringWidths = formattedStrings.stringWidths;
                                }

                                scope.renderContext.finish();
                                scope.svf.minLineWidth = scope.renderContext.currentVbb.minLineWidth;
                                scope.svf.maxObjectNumber = scope.renderContext.maxDbId;
                                scope.onGeomLoadDone();

                                var t1 = performance.now();
                                const pdfLoadTime = t1 - t0;
                                console.log("PDF load time", pdfLoadTime);

                                const firstPixelTime = scope.t1_firstPixel - t0;
                                console.log("First Pixel Time", firstPixelTime);

                                const model = scope.model;
                                const geomList = model.getGeometryList();

                                const dataToTrack = {
                                    load_time: scope.loadTime,
                                    pdf_load_time: pdfLoadTime,
                                    polygons: geomList.geomPolyCount,
                                    fragments: model.getFragmentList().getCount(),
                                    mem_usage: geomList.gpuMeshMemory,
                                    time_to_first_pixel: firstPixelTime,
                                    total_raster_pixels: model.getDocumentNode()?.data?.totalRasterPixels,
                                    viewable_type: '2d',
                                    url: scope.currentLoadPath,
                                    urn: scope.svfUrn,
                                };
                                avp.analytics.track('viewer.model.loaded', dataToTrack);

                                //These are needed in order to free the PDF loader context (it caches all PDF opcodes
                                //in a giant array). Ideally we will modify the pdf.js library to not accumulate opcodes
                                //that are already processed, so we don't spike memory at load time.
                                renderTask._canvas = null;
                            }).catch(err => {
                                // Usually happens when the switching to another PDF page while
                                // the current one is still getting rendered.
                                avp.logger.log(err);
                            }).finally(()=>{
                                scope._renderTask = null;
                                page.cleanup();
                                scope.cleanup();
                            });
                        });
                    });
            });
        }).catch((error) => {
            return Promise.reject(error);
        });
    }

    Promise.resolve().then(
        () => {
            if (options.bubbleNode && typeof (options.bubbleNode.getRootNode().data.getPDF) == "function" && options.bubbleNode.getRootNode().data.getPDF()) {
                scope.pdf = options.bubbleNode.getRootNode().data.getPDF();
                scope.loading = false;
                renderPage(options.bubbleNode.data.page);
            } else {
                var pdfTask = PDFJS.getDocument(params);
                pdfTask.onProgress = ({ loaded, total }) => {
                    const percentLoaded = Math.round((loaded * 100) / total);
                    scope.viewer3DImpl.signalProgress(percentLoaded, av.ProgressState.LOADING);
                };
                // keep pdfTask reference for cleanup later
                scope.pdfTask = pdfTask;

                return pdfTask.promise.then(pdf => {
                    scope.pdf = pdf;
                    scope.loading = false;
                    var pageNumber = options.page || parseInt(avp.getParameterByName("page")) || 1;

                    if (scope.options && !scope.options.bubbleNode) {
                        var pdfMain = scope.createPDFDocument(pdf);
                        scope.options.bubbleNode = pdfMain.getRoot().children[pageNumber - 1];
                    }

                    return renderPage(pageNumber);
                }).catch((error) => {
                    onSuccess && onSuccess(error);
                });
            }

        }
    );

    return true;
};

PDFLoader.prototype.loadOnDemandFonts = function (page, fe) {
    const commonObjs = page.commonObjs._objs;
    const loadedFonts = {};
    const loadPromises = [];
    for (let name in commonObjs) {
        const font = commonObjs[name];
        if (font?.data?.missingFile) {
            const fontName = font?.data?.name;
            if (fontName) {
                const fontDescriptor = fe.getOnDemandFont(fontName);
                if (fontDescriptor && !loadedFonts[fontDescriptor.name]) {
                    loadPromises.push(fe.loadFont(fontDescriptor));
                    loadedFonts[fontDescriptor.name] = true;
                }
            }
        }
    }

    if (loadPromises.length === 0) {
        return;
    }

    return Promise.all(loadPromises);
};


/**
 * Loop all the operator command to detect whether it is a circle.
 */
PDFLoader.prototype.detectCircle = function (page) {
    const threshold = 0.001;
    const magic = (4 / 3) * Math.tan(Math.PI / 8);
    const magic2 = magic * magic;
    let p1 = new THREE.Vector2();
    let p2 = new THREE.Vector2();
    let p3 = new THREE.Vector2();
    let p4 = new THREE.Vector2();

    function equal(a, b) {
        return Math.abs(a - b) <= threshold;
    }

    function relativelyEqual(a, b) {
        return a > 0 && b > 0 && equal(Math.abs(a-b)/a, threshold);
    }

    /**
     *  if it is a primitive circle
     *  it will have a 13, 15, 15, 15, 15, [18]
     *  Reference: https://stackoverflow.com/questions/1734745/how-to-create-circle-with-b%C3%A9zier-curves
     * @param {*} fnArray
     * @param {*} argsArray
     */
    function checkPattern(fnArray, argsArray) {
        if ((fnArray.length == 5 || fnArray.length == 6) && argsArray.length == 26) {
            if (fnArray[0] == 13 && fnArray[1] == 15 && fnArray[2] == 15 && fnArray[3] == 15 && fnArray[4] == 15 && (fnArray[5] == 18 || fnArray[5] == undefined)) {
                // do the math to figure out whether it is a circle
                for (let i = 0; i < argsArray.length - 6; i += 6) {
                    p1.set(argsArray[i], argsArray[i + 1]);
                    p2.set(argsArray[i + 2], argsArray[i + 3]);
                    p3.set(argsArray[i + 4], argsArray[i + 5]);
                    p4.set(argsArray[i + 6], argsArray[i + 7]);
                    if (!checkQuaterCircle(p1, p2, p3, p4)) {
                        return false;
                    }
                }

                return true;
            }
        }
        return false;
    }

    function checkQuaterCircle(p1, p2, p3, p4) {
        let p12 = p2.clone().sub(p1);
        let p43 = p3.clone().sub(p4);
        let l12 = p12.lengthSq();
        let l43 = p43.lengthSq();
        let radius = p4.distanceToSquared(p1) / 2;

        let result = l12 > 0 && l43 > 0 && relativelyEqual(l12, l43)
            && equal(p12.normalize().dot(p43.normalize()), 0)
            && equal(l12 / radius, magic2)
            && equal(l43 / radius, magic2);

        return result;
    }

    return this.getOperatorList(page).then((ops) => {
        let circleInfo = {};
        for (let i = 0; i < ops.fnArray.length; i++) {
            if (ops.fnArray[i] == PDFJS.OPS.constructPath) {
                // need try to detect whether there is circle in this command
                let fn1 = ops.argsArray[i][0];
                let args1 = ops.argsArray[i][1];

                if (checkPattern(fn1, args1)) {
                    p1.set(args1[0], args1[1]);
                    p2.set(args1[6], args1[7]);
                    p3.set(args1[12], args1[13]);
                    p4.set(args1[18], args1[19]);

                    let p12 = p2.clone().sub(p1);
                    let p23 = p3.clone().sub(p2);

                    let l12 = p12.lengthSq();
                    let l23 = p23.lengthSq();
                    let l34 = p3.distanceToSquared(p4);
                    let l41 = p4.distanceToSquared(p1);

                    // check the 4 points is a square
                    if(relativelyEqual(l12, l23) && relativelyEqual(l34, l41) && relativelyEqual(l12, l41) && equal(p12.normalize().dot(p23.normalize()), 0)) {
                        let x = 0, y = 0;
                        for (var k = 0; k < args1.length - 6; k += 6) {
                            x += args1[k];
                            y += args1[k + 1];
                        }

                        circleInfo[i] = [x / 4, y / 4];
                    }
                }
            }
        }

        this.circleInfo = circleInfo;
    });
};


PDFLoader.prototype.processReceivedMesh = function (mdata) {

    //Find all fragments that instance this mesh
    var meshid = mdata.packId + ":" + mdata.meshIndex;

    var svf = this.svf;
    var fragments = svf.fragments;

    var fragIndexes = fragments.mesh2frag[meshid];
    if (fragIndexes === undefined) {
        avp.logger.warn("Mesh " + meshid + " was not referenced by any fragments.");
        return;
    }
    if (!Array.isArray(fragIndexes))
        fragIndexes = [fragIndexes];

    var rm = this.model;

    // Background dbid is -1. Hide it when hideBackground is set.
    // This line has to be before calling meshToGeometry, because mdata.mesh is set to null inside of it.
    if (mdata.mesh.dbIds[-1] && this.options.hideBackground) {
        rm.changePaperVisibility(false);
    }

    //Convert the received mesh to THREE buffer geometry
    const geometry = avp.BufferGeometryUtils.meshToGeometry(mdata);


    // Add geometry to ensure PDF data is taken into account for gpu memory limits
    const geomId = rm.getGeometryList().addGeometry(geometry, fragIndexes.length, mdata.meshIndex + 1);

    const polyCount = avp.getPolygonCount(geometry);

    //For each fragment, add a mesh instance to the renderer
    for (var i = 0; i < fragIndexes.length; i++) {
        var fragId = 0 | fragIndexes[i];

        //We get the matrix from the fragments and we set it back there
        //with the activateFragment call, but this is to maintain the
        //ability to add a plain THREE.Mesh -- otherwise it could be simpler
        rm.getFragmentList().getOriginalWorldMatrix(fragId, this.tmpMatrix);

        if (this.options.placementTransform) {
            this.tmpMatrix = new THREE.Matrix4().multiplyMatrices(this.options.placementTransform, this.tmpMatrix);
        }

        var materialId = fragments.materials[fragId].toString();

        if (fragments.polygonCounts)
            fragments.polygonCounts[fragId] = polyCount;

        var m = this.viewer3DImpl.setupMesh(this.model, geometry, materialId, this.tmpMatrix);
        rm.activateFragment(fragId, m);
    }

    //don't need this mapping anymore.
    fragments.mesh2frag[meshid] = null;

    //Repaint and progress reporting
    fragments.numLoaded += fragIndexes.length;

    this.viewer3DImpl.signalNewGeometryAdded(geomId, this.model);

    if (!this.t1_firstPixel) {
        this.t1_firstPixel = performance.now();
    }

    //repaint every once in a while -- more initially, less as the load drags on.
    if (fragments.numLoaded > fragments.nextRepaint) {
        //avp.logger.log("num loaded " + fragments.numLoaded);
        svf.numRepaints++;
        fragments.nextRepaint += 6 * Math.pow(1.75, svf.numRepaints);
        this.viewer3DImpl.api.dispatchEvent({type: av.LOADER_REPAINT_REQUEST_EVENT, loader: this, model: this.model});
    }
};

PDFLoader.prototype.processReceivedMesh2D = function (mesh, mindex) {

    var mdata = { mesh: mesh, is2d: true, packId: "0", meshIndex: mindex };

    var meshId = "0:" + mindex;

    var frags = this.svf.fragments;

    //Remember the list of all dbIds referenced by this mesh.
    //In the 2D case this is 1->many (1 frag = many dbIds) mapping instead of
    // 1 dbId -> many fragments like in the SVF 3D case.
    var dbIds = Object.keys(mdata.mesh.dbIds).map(function (item) { return parseInt(item); });
    frags.fragId2dbId[mindex] = dbIds;

    //TODO: dbId2fragId is not really necessary if we have a good instance tree for the 2D drawing (e.g. Revit, AutoCAD)
    //so we can get rid of this mapping if we can convert Viewer3DImpl.highlightFragment to use the same logic for 2D as for 3D.
    for (var j = 0; j < dbIds.length; j++) {
        var dbId = dbIds[j];
        var fragIds = frags.dbId2fragId[dbId];
        if (Array.isArray(fragIds))
            fragIds.push(mindex);
        else if (typeof fragIds !== "undefined") {
            frags.dbId2fragId[dbId] = [fragIds, mindex];
        }
        else {
            frags.dbId2fragId[dbId] = mindex;
        }
    }

    const modelFrags = this.model.getFragmentList();

    frags.mesh2frag[meshId] = mindex;
    mesh.material.modelScale = this.modelScale;
    mesh.material.doNotCut = this.options.doNotCut || modelFrags?.getDoNotCut();
    if (modelFrags?.viewBounds) {
        const bounds = modelFrags.viewBounds;
        mesh.material.viewportBounds = new THREE.Vector4(bounds.min.x, bounds.min.y, bounds.max.x, bounds.max.y);
    }

    frags.materials[mindex] = this.viewer3DImpl.matman().create2DMaterial(this.model, mesh.material);

    frags.length++;

    this.processReceivedMesh(mdata);

};

PDFLoader.prototype.onModelRootLoadDone = function (svf) {

    //In the 2d case we create and build up the fragments mapping
    //on the receiving end.
    svf.fragments = {};
    svf.fragments.mesh2frag = {};
    svf.fragments.materials = [];
    svf.fragments.fragId2dbId = [];
    svf.fragments.dbId2fragId = [];
    svf.fragments.length = 0;
    svf.fragments.initialized = true;


    svf.geomPolyCount = 0;
    svf.instancePolyCount = 0;
    svf.fragments.numLoaded = 0;
    svf.fragments.nextRepaint = 5;
    svf.meshCount = 0;

    svf.nextRepaintPolys = 10000;
    svf.numRepaints = 0;

    svf.urn = this.svfUrn;
    svf.acmSessionId = this.acmSessionId;

    svf.basePath = "";
    var lastSlash = this.currentLoadPath.lastIndexOf("/");
    if (lastSlash !== -1)
        svf.basePath = this.currentLoadPath.substr(0, lastSlash + 1);

    svf.loadOptions = this.options;

    var t1 = Date.now();
    avp.logger.log("PDF root load: " + (t1 - this.t0));

    this.t1 = t1;

    //The BBox object loses knowledge of its
    //type when going across the worker thread boundary...
    svf.bbox = new THREE.Box3().copy(svf.bbox);
    svf.modelSpaceBBox = svf.bbox.clone();

    if (svf.placementTransform) {
        svf.bbox.applyMatrix4(svf.placementTransform);
    }

    //Create the API Model object and its render proxy
    var model = this.model = new av.Model(svf);
    model.loader = this;

    model.initialize();

    // Update viewport bounds according to model clipped bounds. (See LmvCanvasContext)
    model.setViewportBounds(this.viewer3DImpl.matman(), svf.modelSpaceBBox);

    if (!this.options.skipPropertyDb) {
        this.svf.propDbLoader = new avp.PropDbLoader(this.sharedDbPath, this.model, this.viewer3DImpl.api);
    }

    avp.logger.log("scene bounds: " + JSON.stringify(svf.bbox));

    var metadataStats = {
        category: "metadata_load_stats",
        urn: svf.urn,
        layers: svf.layerCount
    };
    avp.logger.track(metadataStats);

    this.viewer3DImpl.setDoNotCut(model, !!this.options.doNotCut);

    this.viewer3DImpl.signalProgress(5, av.ProgressState.ROOT_LOADED, model);
};


PDFLoader.prototype.onGeomLoadDone = function () {
    this.svf.loadDone = true;

    // Don't need these anymore
    this.svf.fragments.entityIndexes = null;
    this.svf.fragments.mesh2frag = null;

    var t2 = Date.now();
    var msg = "Fragments load time: " + (t2 - this.t1);
    this.loadTime = t2 - this.t0;

    if (!this.options.skipPropertyDb) {
        this.loadPropertyDb();
    }

    avp.logger.log(msg);

    var modelStats = {
        category: "model_load_stats",
        is_f2d: true,
        has_prism: this.viewer3DImpl.matman().hasPrism,
        load_time: this.loadTime,
        geometry_size: this.model.getGeometryList().geomMemory,
        meshes_count: this.model.getGeometryList().getCount(),
        urn: this.svfUrn
    };
    avp.logger.track(modelStats, true);

    this.viewer3DImpl.onLoadComplete(this.model);
};

PDFLoader.prototype.loadPropertyDb = function() {

    if (this.svf.propDbLoader) {
        this.svf.propDbLoader.load();
    }
};

PDFLoader.prototype.is3d = function () {
    return false;
};

PDFLoader.prototype.cleanup = function() {
    if (this.renderContext) {
        this.renderContext.destroy();
        this.renderContext = null;
    }

    if(this.pdf) {
        this.pdf.cleanup();
    }
};

PDFLoader.prototype.getOperatorList = function (page) {
    // Use the same 'display' intent for everything, in order to save memory.
    return page.getOperatorList('display');
};
